import { appList, list } from '@/api/channel.js';
import clip from '@/utils/clipboard'; // use clipboard directly

export default {
  data: function data() {
    return {
      activeIndex: '',
      currentPage: 1,
      tableData: [],
      value: '',
      pageSize: 10,
      aid: '',
      cid: '',
      companyList: [],
      appsList: [],
      appValue: '',
      total: 0,
      loading: false
    };
  },
  mounted: function mounted() {
    this.getApp();
    this.getList();
  },
  methods: {
    handleCopy: function handleCopy(text, event) {
      clip(text, event);
    },
    clipboardSuccess: function clipboardSuccess() {
      this.$message({
        message: '复制成功',
        type: 'success',
        duration: 1500
      });
    },
    onChangeStatus: function onChangeStatus(item) {
      console.log(item);
      this.$router.push({
        path: "/details",
        query: {
          url: item
        }
      });
    },
    goDetail: function goDetail(item) {
      this.$router.push({
        path: "/details/".concat(item.id)
      });
    },
    transformTime: function transformTime(timestamp) {
      if (timestamp) {
        timestamp = timestamp * 1000;
        var time = new Date(timestamp);
        var y = time.getFullYear(); // getFullYear方法以四位数字返回年份

        var M = time.getMonth() + 1; // getMonth方法从 Date 对象返回月份 (0 ~ 11)，返回结果需要手动加一

        var d = time.getDate(); // getDate方法从 Date 对象返回一个月中的某一天 (1 ~ 31)

        var h = time.getHours(); // getHours方法返回 Date 对象的小时 (0 ~ 23)

        var m = time.getMinutes(); // getMinutes方法返回 Date 对象的分钟 (0 ~ 59)

        var s = time.getSeconds(); // getSeconds方法返回 Date 对象的秒数 (0 ~ 59)

        return y + '-' + M + '-' + d + ' ' + h + ':' + m + ':' + s;
      } else {
        return '';
      }
    },
    onChangeApp: function onChangeApp(index) {
      this.aid = index;
      this.getList();
    },
    getList: function getList() {
      var _this = this;

      this.loading = true;
      list({
        page: this.currentPage,
        size: this.pageSize,
        aid: this.aid
      }).then(function (response) {
        if (response.code === 200) {
          _this.total = response.count;
          _this.loading = false;
          _this.tableData = response.data.map(function (item) {
            item.Status = item.status === '0' ? '无效' : '有效';
            item.valid = item.status === '0' ? '否' : '是';
            item.create_time = _this.transformTime(item.create_time);
            return item;
          });
        }
      }).catch(function (e) {
        _this.loading = false;
      });
    },
    getApp: function getApp() {
      var _this2 = this;

      appList({}).then(function (response) {
        if (response.code === 200) {
          // this.companyList = response.data
          _this2.appsList = response.data;
        }
      });
    },
    createUrl: function createUrl() {
      this.$router.push({
        path: '/create'
      });
    },
    handleSelect: function handleSelect(e) {
      this.activeIndex = e;
      this.getList();
    },
    handleSizeChange: function handleSizeChange(e) {
      this.pageSize = e;
      this.getList();
    },
    handleCurrentChange: function handleCurrentChange(e) {
      this.currentPage = e;
      this.getList();
    }
  }
};